const handleLoansRoutes = (location, type, category, applicationId) => {
  const params = new URLSearchParams(location.search);
  let paramsLocal = {};
  category = category
    .replace('_application', '')
    .replace('_loans', '')
    .replace('_loan', '');
  paramsLocal['application_id'] = applicationId;
  paramsLocal['type'] = type;
  paramsLocal['category'] = category;
  let newUrl = location.pathname + '?';
  for (const key in paramsLocal) {
    if (Object.hasOwnProperty.call(paramsLocal, key)) {
      if (paramsLocal[key]) {
        newUrl += `&&${key}=${paramsLocal[key]}`;
      }
    }
  }
  return newUrl;
};

export default handleLoansRoutes;
