const RemarksCard = ({ title, remarks }) => {
  return (
    <div className='remarks-card-container'>
      <h4 className='h6 f20 text-bold text-capitalize'>{title}</h4>
      <ol>
        {remarks && remarks.map((remark, key) => <li key={key}>{remark}</li>)}
      </ol>
    </div>
  );
};

export default RemarksCard;
