import * as dayjs from 'dayjs';
import React, { useEffect, useState } from 'react';
import InputBox from '../../FormInputs/InputBox';
import CurrencyInput from '../../FormInputs/CurrencyInput';
import OfferDocumentList from '../../Tables/OfferDocumentList';
import { MAKE_AN_OFFER_TYPES, LOAN_FIELDS } from '../../../constants';
import { useForm } from 'react-hook-form';



const LoanSummary = ({ loanInfo, loanSummary, props }) => {


  const [offerDocuments, setOfferDocuments] = useState([]);
  useEffect(() => {
    if (loanSummary) {
      let documents = { ...offerDocuments };
      for (const key in loanSummary) {
        if (key === MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]) {
          if (loanSummary[key]) {
            documents[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]] = [];
            documents[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]][0] = {
              type: 'Sanction Letter',
              url: loanSummary[key],
              name:loanSummary['sanction_letter_filename'],
            };
          }

          continue;
        }

        if (
          key === MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments] &&
          MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments].length > 0
        ) {
          documents[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments]] = [];
            documents[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments]][0] = {
              type: 'Other Documents',
              url: loanSummary[key],
              name: loanSummary['other_documents_filename'],
            };
          // documents.push({
          //   type: 'Other Documents',
          //   url: props.defaultValues[key],
          // });
          continue;
        }
      }
      setOfferDocuments(documents);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loanSummary]);

  return (
    <div className='mt-4'>
      <div className='row'>
        <div className='col-md-7 px-5'>
          <h4 className='f16 text-bold'>
            <span className='text-muted'>Borrower</span>
            <span className='pl-5 text-bold'>{loanInfo.fponame}</span>
          </h4>
        </div>
        <div className='col-md-5'>
          <h4 className='f16 text-bold'>
            <span className='text-muted'>Loan Start Date</span>
            <span className='ml-3 text-bold'>
              {loanInfo.loanstartdate
                ? dayjs(loanInfo.loanstartdate).format('D-M-YYYY')
                : 'NIL'}
            </span>
          </h4>
        </div>
      </div>
      <br />
      <div className='px-3'>
        <h4 className='form-section-header mt-4 mb-2 fs-16'>Offer details</h4>
        <section className='d-flex flex-wrap'>
          <InputBox
            label='Purpose'
            className={''}
            value={loanSummary.loanpurpose}
            readOnly={true}
            labelOnly={true}
          />

          <CurrencyInput
            label='Loan Amount'
            className=''
            readOnly={true}
            value={loanSummary.loanamount}
            labelOnly={true}
          />

          <InputBox
            label='Rate Of Interest (%)'
            className={''}
            value={loanSummary.tenure}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Tenure'
            className={''}
            value={loanSummary.tenure}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Tenure Category'
            className={''}
            value={loanSummary.tenurecategory}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Processing Fee(INR)'
            className={''}
            value={loanSummary.processingfees}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Guarantee'
            className={''}
            value={loanSummary.guarantee}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Repayment date'
            className={''}
            value={loanSummary.repaymentdate && dayjs(loanSummary.repaymentdate).format('YYYY-MM-DD')}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Repayment type'
            className={''}
            value={loanSummary.repaymenttype}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Loan option'
            className={''}
            value={loanSummary.loanoption}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Validity Of limit'
            className={''}
            value={loanSummary.validity_limit}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Validity Of limit category'
            className={''}
            value={loanSummary.validitylimitcategory}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Margin %'
            className={''}
            value={loanSummary.margin}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Primary Security'
            className={''}
            value={loanSummary.primary_security}
            readOnly={true}
            labelOnly={true}
          />
          <InputBox
            label='Collateral Security'
            className={''}
            value={loanSummary.collateral_security}
            readOnly={true}
            labelOnly={true}
          />
        </section>
      </div>
      {offerDocuments && (
          <div className='px-4 no-padding-mobile my-3'>
            <OfferDocumentList
              documents={offerDocuments}
              title={loanSummary ? 'Documents' : 'Selected documents'}
               readOnly={props}
            />
          </div>
        )}
    </div>
  );
};

export default LoanSummary;
