import { useSelector } from 'react-redux';
import { useLocation } from 'react-router';
import ShadowBox from '../../../../components/ShadowBox';

import {
  APPLICATION_STATUS,
  STATUS_REMARKS,
  application_status_remarks,
} from '../../../../constants';
import Form from '../../../../components/Forms/FPO/Form';

const CatalyticInfrastructure = () => {
  const location = useLocation();
  const fpoState = useSelector((state) => state.fpo);
  const params = new URLSearchParams(location.search);
  let loanId = params.get('loanId');
  return (
    <section className='primary-bg'>
      <div className='container pb-7'>
        <div className='container '>
          <h4 className='mb-4'>
            Apply for a New Loan{' '}
            <span className='pull-right h5 text-bold text-muted'>
              <span>Application ID ({loanId})</span>
              {fpoState &&
                fpoState.selectedLoan &&
                fpoState.selectedLoan.loanInfo &&
                fpoState.selectedLoan.loanInfo.status_remarks &&
                fpoState.selectedLoan.loanInfo.status_remarks ===
                  application_status_remarks[
                    STATUS_REMARKS.sentBackByAdmin
                  ] && (
                  <>
                    <br />
                    <span className='h6 f14 '>
                      Sendback Reason:{' '}
                      <span className='re-apply'>
                        {fpoState.selectedLoan.loanInfo.remarks}
                      </span>
                    </span>
                  </>
                )}
            </span>
          </h4>
          {fpoState &&
          fpoState.selectedLoan &&
          fpoState.selectedLoan.isLoading ? (
            <div className=''>Loading</div>
          ) : (
            <ShadowBox className='p-0'>
              <Form
                readOnly={
                  fpoState.selectedLoan?.loanInfo?.applicationstatus ===
                  APPLICATION_STATUS.APPLIED
                }
                loanInfo={fpoState.selectedLoan?.loanInfo}
                documents={fpoState.documents}
                formType='term-loan-catalyticInfrastructure'
                loanId={loanId}
              />
            </ShadowBox>
          )}
        </div>
      </div>
    </section>
  );
};

export default CatalyticInfrastructure;
